import { Component, OnInit } from '@angular/core';
import { Validators, FormControl, FormGroup, FormArray, FormBuilder } from '@angular/forms';
import { CategoryService } from '../services/category.service';
import { ConfirmationService } from 'primeng/components/common/confirmationservice';
@Component({
selector: 'app-category',
templateUrl: './category.component.html',
styleUrls: ['./category.component.scss']
})
export class CategoryComponent implements OnInit {
displayDialog : boolean = false;
allCategory : any ;
addCategoryForm : FormGroup;
items: FormArray;
isSubmited : boolean = false;
constructor(private formbuilder : FormBuilder,private _service : CategoryService,   private confirmationService: ConfirmationService) { }

ngOnInit() {
this.addCategoryForm =this.formbuilder.group({
category_name: ['', [Validators.required, Validators.minLength(3), Validators.maxLength(12)]],
sub_category: this.formbuilder.array([])
})
this.getCategory();
}
createCtaegory(){
return this.formbuilder.group({
name : ''
});
}
getCategory(){
  this._service.getAllCategory().subscribe(res=>{
    if(res.code = 200){
      console.log(res);
      this.allCategory = res.data;
    }
  })
}
openDialog(){
this.displayDialog = true;
}
add(){
  // if (this.addCategoryForm.invalid) {
  //   this.isSubmited = true;
  //   return;
  // }
console.log(this.addCategoryForm.value);
this._service.addCategory(this.addCategoryForm.value).subscribe(res=>{
  if(res.code == 200){
    console.log(res)
    this.getCategory();
    this.displayDialog = false;
  }
  
})
}
delete(id){
  
  this.confirmationService.confirm({
    
          message: 'Are you sure that you want to perform this action?',
          header: 'Confirmation',
          icon: 'fa fa-question-circle',
          accept: () => {
            this._service.deleteCategory(id).subscribe(res =>{
              if(res.code == 200){
                this.getCategory();
              }
            })
        },reject: () => {
         
      }
      });
}
deleteRow(index: number){
const control = <FormArray>this.addCategoryForm.controls['sub_category'];
// remove the chosen row
control.removeAt(index);
}
addtext(){
const control = <FormArray>this.addCategoryForm.controls['sub_category'];
control.push(this.createCtaegory());

}
}
