import { Component, OnInit } from '@angular/core';
import { navItems } from '../list/_nav';
import {  Router } from "@angular/router";
import { LoginService } from '../../common/signup-signin/services/login.service';
import { ToastrService } from 'ngx-toastr';

@Component({
  selector: 'app-landing',
  templateUrl: './landing.component.html',
  styleUrls: ['./landing.component.scss']
})
export class LandingComponent implements OnInit {

  ngOnInit(): void {}
  public navItems = navItems;
  public sidebarMinimized = true;
  private changes: MutationObserver;
  public element: HTMLElement = document.body;
  constructor(private router: Router,private _service : LoginService,private toastr : ToastrService) {

    this.changes = new MutationObserver((mutations) => {
      this.sidebarMinimized = document.body.classList.contains('sidebar-minimized');
    });

    this.changes.observe(<Element>this.element, {
      attributes: true
    });
  }
  logout()
  {
    this._service.logout().subscribe(res=>{
      if(res.code == 200){
        localStorage.removeItem("token");
        this.toastr.success(res.message)
        this.router.navigate(["login"]);
      }
      else{
        localStorage.removeItem("token");
        this.toastr.success(res.message)
        this.router.navigate(["login"]);
      }
    })
  }
}
