import { Component, OnInit } from '@angular/core';
import { Validators, FormControl, FormGroup, FormArray, FormBuilder } from '@angular/forms';
import {ProfileserviceService} from '../../services/profileservice.service'
import { Router } from '@angular/router';
@Component({
  selector: 'app-viewprofile',
  templateUrl: './viewprofile.component.html',
  styleUrls: ['./viewprofile.component.scss']
})
export class ViewprofileComponent implements OnInit {
  public viewProfileForm: FormGroup;
  firstName : any;
  lastName:any;
  dob:any;
  email:any;
  constructor(private ProfileService:ProfileserviceService,private router : Router,private formbuilder : FormBuilder) { }

  ngOnInit() {
    this.viewProfileForm =this.formbuilder.group({
      email: ['', [Validators.required]],
      firstName: ['', [Validators.required, Validators.minLength(3), Validators.maxLength(12)]],
      lastName: ['', [Validators.required, Validators.minLength(3), Validators.maxLength(12)]],
      dob: ['', [Validators.required, Validators.minLength(3), Validators.maxLength(12)]],
      
    })
    this.viewprofile()
  }
  viewprofile(){
    this.ProfileService.getProfile().subscribe(res=>{  
        if (res.code === 200) {
          console.log(">>>>>>>>>",res)
          
          this.email=res.data.email,
          this.firstName = res.data.firstName,
          this.lastName= res.data.lastName,
          this.dob= res.data.dob,
          this.viewProfileForm.patchValue({
            email:res.data.email,
            firstName: res.data.firstName,
            lastName: res.data.lastName,
            dob: res.data.dob,

         
          });
        }
        
      })
  }

//   updateProfile(){
//     this.ProfileService.updateProfile(this.editProfileForm.value).subscribe(res=>{
// console.log("update profile response>>>>>>>>",res)
//     })
//   }


}
